body {
  background: white;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;
  color: black;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; }
  body.first-screen {
    background: #D9151F; }

.center {
  max-width: 1170px;
  width: calc(100% - 30px);
  margin-right: auto;
  margin-left: auto;
  padding-right: 15px;
  padding-left: 15px; }

*:hover,
*:focus,
*:active {
  outline: 0;
  outline-offset: 0; }

p {
  margin: 0; }

.main {
  flex-grow: 1;
  flex-shrink: 0;
  display: none;
  flex-direction: column;
  justify-content: center;
  padding-top: 25px;
  padding-bottom: 25px; }
  .main.active {
    display: flex; }

.main_logo {
  margin-bottom: 60px; }

.main_title {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 120%;
  text-transform: uppercase;
  color: white;
  margin-bottom: 60px; }

.main_table {
  display: flex;
  flex-direction: column;
  background: white;
  border-radius: 15px;
  max-width: 530px;
  width: 100%;
  padding: 30px;
  box-sizing: border-box; }

.main_row {
  display: flex;
  flex-direction: row;
  align-items: flex-end; }
  .main_row--first {
    margin-bottom: 25px; }

.main_cell {
  padding-left: 50px;
  box-sizing: border-box;
  position: relative;
  flex-grow: 1;
  min-height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center; }
  .main_cell--title, .main_cell--subtitle {
    padding-left: 0;
    align-items: baseline; }
  .main_cell--title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 110%;
    color: #0F0E0E; }
  .main_cell--subtitle {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 120%;
    color: #F03226;
    text-align: right; }
    .main_cell--subtitle span {
      font-size: 60px;
      margin-left: 8px; }

.main_img {
  position: absolute;
  top: 0;
  left: 0; }

.main_text {
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 120%;
  color: #000000;
  display: flex;
  flex-direction: column; }
  .main_text span {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 4px; }

.main_description {
  margin-top: 30px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 120%;
  color: #000000;
  margin-bottom: 24px; }

.main_button {
  background: #F03226;
  box-shadow: 0px 0px 40px rgba(240, 50, 38, 0.4);
  border-radius: 5px;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 140%;
  color: #FFFFFF;
  position: relative;
  text-decoration: none;
  align-self: flex-start;
  min-width: 240px;
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none; }
  .main_button:hover {
    cursor: pointer; }

@media (max-width: 767px) {
  .main {
    justify-content: flex-start; }
  .main_logo {
    margin-bottom: 17px;
    width: 80px; }
  .main_title {
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 26px;
    line-height: 115%;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-bottom: 20px; }
    .main_title br {
      display: none; }
  .main_row {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start; }
    .main_row--first {
      margin-bottom: 0; }
  .main_row--first {
    order: 1;
    align-self: center;
    align-items: center; }
  .main_button {
    order: 2; }
  .main_description {
    order: 3; }
  .main_table {
    padding: 25px 20px; }
  .main_row--two {
    order: 4; }
    .main_row--two .main_cell:first-child {
      margin-bottom: 20px; }
  .main_cell {
    padding-left: 50px;
    min-height: 40px; }
    .main_cell--title, .main_cell--subtitle {
      padding-left: 0;
      align-items: baseline;
      min-height: auto; }
    .main_cell--title {
      font-size: 22px;
      margin-bottom: 10px; }
      .main_cell--title br {
        display: none; }
    .main_cell--subtitle {
      font-size: 26px;
      text-align: center;
      margin-bottom: 12px; }
      .main_cell--subtitle span {
        font-size: 45px;
        margin-left: 8px; }
  .main_img {
    width: 40px;
    height: 40px; }
  .main_text {
    font-size: 14px;
    line-height: 120%; }
  .main_description {
    font-size: 14px;
    line-height: 120%;
    margin-top: 0;
    margin-bottom: 20px; }
    .main_description br {
      display: none; }
  .main_button {
    align-self: stretch;
    margin-bottom: 15px;
    font-size: 21px; } }

.footer {
  position: relative;
  height: 70px;
  display: none; }
  .footer.active {
    display: block; }

.footer_wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end; }

.footer_button {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transition: .5s;
  transform: translateX(-50%);
  z-index: 999; }
  .footer_button:hover {
    cursor: pointer; }

.footer_copyright {
  bottom: -70px;
  transition: .5s;
  font-size: 14px;
  position: relative;
  text-decoration: underline;
  color: white;
  display: inline; }
  .footer_copyright:hover {
    cursor: pointer; }

.footer.open .footer_button {
  bottom: 50px;
  transform: translateX(-50%) rotate(180deg); }

.footer.open .footer_copyright {
  bottom: 10px;
  transition: .5s; }

.footer-information {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 120%;
  padding-top: 10px;
  display: none;
  color: white; }
  .footer-information p {
    margin-bottom: 15px; }
    .footer-information p a {
      color: white; }
  .footer-information.open {
    display: block; }

@media (max-width: 767px) {
  .footer-information {
    font-size: 14px; } }

form {
  width: 100%;
  height: 100%;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1; }
  form.active {
    display: flex; }

.question {
  background: #F6F6F6;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 45px 32px;
